.movieCard{
    position: relative;
    width: 200px;
    text-decoration: none;
    color:white;
    margin:15px 20px;
    transition: 0.2s;
}

.movieCard:hover{
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: scale(1.1);  
    transform: scale(1.1);
    border-radius:5px;
    background:#f55f44;
    color:black;
}

